@media screen(max-width:670px) {
  body {
    font-family: sans-serif;
  }
  input {
    border-radius: 8px;
    border: none;
    padding: 6px 16px;
    outline: none;
  }
  button {
    border-radius: 16px;
    border: none;
    padding: 4px 16px;
  }
  button:hover {
    background-color: pink;
    cursor: pointer;
  }
  li {
    margin-right: 8px;
  }
  .input-area {
    background-color: #cf4444;
    width: 100%;
    height: 30px;
    padding: 8px;
    margin: 8px;
    border-radius: 8px;
  }
  .incomplete-area {
    background-color: aqua;
    width: 100%;
    min-height: 200px;
    padding: 8px;
    margin: 8px;
    border-radius: 8px;
  }
  .complete-area {
    background-color: rosybrown;
    width: 100%;
    min-height: 200px;
    padding: 8px;
    margin: 8px;
    border-radius: 8px;
  }
  .title {
    text-align: center;
    margin-top: 0;
    font-weight: bold;
    color: #666;
  }
  .list-row {
    display: flex;
    align-items: center;
    padding-bottom: 4px;
  }
}
